@use '../abstracts' as *;

/*****************
    Modal
******************/
.modal {
  display: none;
  position: fixed;

  &::before {
    position: fixed;
    content: '';
    inset: 0;
    background-color: $black;
    opacity: 0.7;
  }

  &.show {
    display: block;
  }

  &__container {
    background-color: $white;
    border-radius: $border-radius--medium;
    border: 2px solid $grey-400;
    min-width: 300px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 1.5em;
  }

  h3 {
    margin-bottom: 1em;
  }

  button {
    display: block;
    margin-left: auto;
  }
}
