/*****************
    Reset/Base
******************/
html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: auto;
  min-height: 100vh;
  position: relative;
}

img,
picture {
  display: block;
  max-width: 100%;
}

button,
input:not([type='checkbox']),
textarea {
  appearance: none;
  font: inherit;
}

ul,
ol {
  padding-inline-start: 0;
}

a {
  text-decoration: none;
}

[hidden] {
  display: none !important;
}
